@import url(https://fonts.googleapis.com/css2?family=Merienda&display=swap);
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
@font-face {
	font-family: 'The Historia Demo';
	src: url(/portfolio/static/media/thehistoriademo-webfont.e2db6a1d.woff2) format('woff2'),
		url(/portfolio/static/media/thehistoriademo-webfont.cea51a18.woff) format('woff');
	font-weight: normal;
	font-style: normal;
}
/*
#F7B538 -mustardy
#1F2235 -indigoey
#FE4A57 -reddish
*/
* {
	/* border: 1px solid red; */
	/* outline: 1px solid red; */
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html,
body {
	scroll-behavior: smooth;
	width: 100vw;
	overflow-x: hidden;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.subtitle--red {
	color: #fe4a57;
	font-family: 'The Historia Demo', sans-serif;
	font-weight: 300;
	font-size: clamp(22px, 8vw, 84px);
}

.sub-yellow {
	color: #f7b538;
	font-size: clamp(18px, 3vw, 24px);
	font-weight: 300;
	font-family: 'Merienda', cursive;
}

.sub-yellow--larger {
	color: #f7b538;
	font-size: clamp(20px, 3vw, 26px);
	font-weight: 300;
	font-family: 'Merienda', cursive;
}

