#navbar {
	height: 70px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 22px;
	max-width: 100vw;
	position: relative;
}

.nav-menu {
	display: flex;
	list-style: none;
	text-align: center;
}

.nav-item {
	display: flex;
	align-items: center;
	height: 70px;
	line-height: 25px;
	padding: 15px;
}

.nav-link {
	color: #fe4a57;
	text-decoration: none;
	padding: 8px 14px;
	font-family: 'The Historia Demo', sans-serif;
	font-size: 34px;
}

#hello h2 {
	margin: 0 auto;
	max-width: 900px;
	padding-top: 30px;
}

#hello {
	font-size: 24px;
	color: #f7b538;
	font-family: 'Merienda', cursive;
	padding-bottom: 30px;
	max-width: 90%;
	margin: 50px auto;
}
#hello h1 {
	font-family: 'The Historia Demo';
	font-size: clamp(62px, 10vw, 135px);
	background: linear-gradient(to top, #fa320f, #ffc738);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	font-weight: 500;
}

.social-icons {
	color: #fe4a57;
	font-size: 32px;
	padding-right: 30px;
	display: flex;
}

.social-icons a {
	padding-left: 10px;
}

.social-icons a:visited {
	color: #fe4a57;
}

.nav-link:hover {
	background-color: #f7b538;
	color: #1f2235;
	border-radius: 4px;
	transition: all 0.2s ease-out;
	cursor: pointer;
}

.fa-bars {
	color: #fe4a57;
}
.fa-times:before {
	color: #fe4a57;
}

.menu-icon {
	display: none;
}

@media screen and (max-width: 768px) {
	#navbar {
		position: relative;
	}

	#hello {
		margin-top: 25px;
	}
	.nav-menu {
		display: flex;
		flex-direction: column;
		width: 100vw;
		max-width: 100vw;
		height: 100vh;
		position: absolute;
		top: 0px;
		bottom: 0px;
		left: -100vw;
		opacity: 1;
		transition: all 0.5s ease;
	}

	.nav-item:first-of-type {
		margin-top: 20vh;
	}
	.nav-item {
		height: 14vh;
		display: flex;
		align-items: center;
	}
	.nav-link {
		height: 90%;
	}

	.nav-menu.active {
		position: fixed;
		background: #1f2235;
		left: 0;
		top: 0;
		opacity: 5;
		transition: all 0.5s ease;
		z-index: 5;
	}

	.nav-link {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 16px;
		width: 100vw;
		font-size: 30px;
		margin-left: 0px;
	}

	.nav-link:hover {
		background-color: #f7b538;
		color: #1f2235;
		border-radius: 0;
	}

	.menu-icon {
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		transform: translate(-100%, 60%);
		font-size: 29px;
		cursor: pointer;
	}

	.fa-times {
		font-size: 32px;
	}

	.social-icons {
		width: 90px;
		position: absolute;
		left: calc(50% - 45px);
		font-size: 28px;
	}
}

#about {
	max-width: 1600px;
	margin: 0 auto;
	padding-bottom: 25px;
}

.about {
	display: grid;
	grid-template-columns: 2fr 1fr 2fr;
	position: relative;
	padding-top: 80px;
	display: flex;
	flex-direction: column;
}

.about hr {
	border: 1px solid #fe4a57;
	margin: 0 auto;
	max-width: 65vw;
}

.about-personal {
	display: flex;
}

.about-personal div {
	text-align: justify;
	width: 65%;
	margin: 20px auto;
}

.about-skills ul {
	margin: 40px auto;
}

.about-skills li {
	color: #f7b538;
	font-size: clamp(16px, 3vw, 24px);
	font-weight: 300;
	font-family: 'Merienda', cursive;
	list-style-type: none;
}

.about-personal div hr {
	margin: 0 auto;
}

/* ///////////////////TABLET////////////////////// */

@media screen and (max-width: 768px) {
	.about {
		display: flex;
		flex-direction: column;
		padding-top: 50px;
	}

	.about-personal div {
		width: 80%;
	}
	.about-personal {
		flex-direction: column;
	}
	.about hr {
		max-width: 80vw;
	}
}

/* ///////////////////PHONES////////////////////// */

@media screen and (max-width: 450px) {
	.about {
		padding-top: 20px;
	}
}

#skills {
	padding: 50px;
	position: relative;
	color: #fe4a57;
	font-family: 'The Historia Demo', sans-serif;
	text-align: center;
}

.techstack {
	display: flex;
	flex-flow: row;
	width: 75%;
	max-width: 1080px;
	flex-wrap: wrap;
	justify-content: space-around;
	padding-top: 40px;
	padding: 20px;
	text-align: center;
	margin: 0 auto;
}

.tech-icons {
	margin: 15px auto;
	border-radius: 5px;
}

.techstack p {
	font-family: 'Merienda', cursive;
}

.tech-icons img {
	margin: 10px 20px;
	border-radius: 5px;
	max-height: 95px;
	height: 95px;
}

@media screen and (max-width: 1024px) {
	#skills {
		padding: 40px;
	}
	.techstack {
		width: 95%;
	}

	.tech-icons {
		margin: 5px 5px;
		border-radius: 5px;
	}
	.tech-icons img {
		margin: 5px 5px;
		max-height: 75px;
	}
}
@media screen and (max-width: 720px) {
	#skills {
		padding: 15px;
	}
	.techstack {
		width: 99%;
	}
	.tech-icons img {
		margin: 5px 12px;
		max-height: 40px;
	}
}

#projects {
	padding-top: 0px;
	min-height: 50vh;
	display: grid;
}

.projects-div {
	margin: 20px auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	max-width: 1400px;
}

.project-card {
	height: 200px;
	width: 380px;
	border-radius: 25px;
	margin-bottom: 20px;
	position: relative;
}

.btn {
	height: 30px;
	width: 80px;
	border: 2px solid #fe4a57;
	border-radius: 10px;
	opacity: 1;
	font-size: 16px;
	font-family: 'Merienda', cursive;
}

.card {
	position: relative;
	margin-top: 50px;
	width: 500px;
	height: 300px;
	/* background-image: '../../assets/imgs/MDL.png'; */
	border-radius: 20px;
	font-family: 'The Historia Demo', sans-serif;
	/* overflow: hidden; */
}

.card::before {
	content: '';
	position: absolute;
	top: -4px;
	right: -4px;
	bottom: -4px;
	left: -4px;
	background: linear-gradient(315deg, #f7b538, #0e153800, #fe4a57);
	transform: skewX(5deg) skewY(5deg);
}

.card::after {
	content: '';
	position: absolute;
	top: -4px;
	right: -4px;
	bottom: -4px;
	left: -4px;
	background: linear-gradient(315deg, #f7b538, #0e153800, #fe4a57);
	transform: skewX(5deg) skewY(5deg);
	-webkit-filter: blur(29px);
	        filter: blur(29px);
}

.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0);
	transition: all 0.5s ease;
	z-index: 3;
}

.card:hover .overlay {
	display: block;
	background: rgba(0, 0, 0, 0.8);
}

.card img {
	position: absolute;
	width: 500px;
	height: 300px;
	left: 0;
	z-index: 3;
}

.title,
.proj-description {
	position: absolute;
	width: 500px;
	top: 220px;
	font-weight: 300;
	text-align: center;
	transition: top 0.5s ease;
	z-index: 5;
	opacity: 0;
}

.title {
	font-size: 32px;
	color: #fe4a57;
}

.proj-description {
	font-family: 'Merienda', cursive;
	font-size: clamp(15px, 2vw, 18px);
	text-align: center;
	padding: 0 30px;
	line-height: 1.5;
	color: #f7b538;
}

.card:hover p {
	opacity: 1;
}

.card:hover .proj-description {
	top: 65px;
	color: #f7b538;
}
.card:hover .title {
	top: 35px;
}

.button-wrap {
	position: absolute;
	width: 500px;
	left: 0;
	bottom: 20px;
	text-align: center;
	opacity: 0;
	transition: opacity 0.35s ease;
	z-index: 5;
}
.card:hover .button-wrap {
	opacity: 1;
}
.btn {
	background-color: #1f2235;
	color: #fe4a57;
	z-index: 5;
}
.btn:hover {
	cursor: pointer;
	background-color: #f7b538;
	color: #1f2235;
	border: 2px solid #1f2235;
}

.button-wrap a {
	width: 100px;
	padding: 5px;
	text-align: center;
	z-index: 1;
}

/* //////////////////////////////////// */
/* /////////////////TABLET///////////// */
/* //////////////////////////////////// */
@media screen and (max-width: 768px) {
	.card {
		position: relative;
		margin-top: 50px;
		width: 420px;
		height: 280px;
		border-radius: 20px;
		font-family: 'The Historia Demo', sans-serif;
		/* overflow: hidden; */
	}
	.card img {
		width: 420px;
		height: 280px;
	}
	.overlay {
		display: block;
		background: rgba(0, 0, 0, 0.65);
	}
	.title {
		width: 420px;
		top: 20px;
		left: 0;
		font-weight: 300;
		font-size: 35px;
		z-index: 1;
		opacity: 1;
		margin: 0 auto;
	}
	.proj-description {
		padding: 0;
		opacity: 1;
		top: 60px;
		width: 100%;
		margin: 0 auto;
	}
	.button-wrap {
		opacity: 1;
		width: 420px;
	}
	.card:hover .title {
		top: 20px;
	}
	.btn {
		background-color: #f7b538;
		color: #1f2235;
	}
}

/* //////////////////////////////////// */
/* ///////////////PHONES/////////////// */
/* //////////////////////////////////// */
@media screen and (max-width: 540px) {
	.card {
		margin-top: 50px;
		width: 360px;
		height: 240px;
		border-radius: 20px;
		/* overflow: hidden; */
	}
	.card img {
		width: 360px;
		height: 240px;
	}
	.title {
		width: 360px;
		top: 20px;
		left: 0;
		font-size: 26px;
		z-index: 1;
		opacity: 1;
		margin: 0 auto;
	}
	.button-wrap {
		opacity: 1;
		width: 360px;
	}
}

.contact {
	display: flex;
	flex-direction: column;
	margin: 0 auto;
	width: 95%;
	align-items: center;
}

.form-and-social {
	display: flex;
	flex-direction: row-reverse;
	justify-content: center;
	margin: 40px auto;
	width: 90%;
	max-width: 1380px;
}

.contact-form {
	flex: 1 1;
	display: flex;
	flex-direction: column;
}

.social {
	flex: 1 1;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
}

.social a,
.social a:visited,
.social a:link {
	text-decoration: none;
	color: #f7b538;
}

.contact-form {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.contact-form :nth-child(1) {
	order: 2;
}
.contact-form :nth-child(2) {
	order: 1;
}
.contact-form :nth-child(3) {
	order: 4;
}
.contact-form :nth-child(4) {
	order: 3;
}
.contact-form :nth-child(5) {
	order: 6;
}
.contact-form :nth-child(6) {
	order: 5;
}
.contact-form :nth-child(7) {
	order: 7;
}

input:focus + label,
textarea:focus + label {
	color: #fe4a57;
}

.contact-form > input,
.contact-form > textarea {
	padding: 10px;
	border-radius: 3px;
	margin-bottom: 20px;
	background: #fff;
	font-size: 16px;
	color: rgb(0, 0, 32);
	outline: none;
	border: 3px solid #f7b538;
}

.contact-form > input:focus,
.contact-form > textarea:focus {
	border: 3px solid #fe4a57;
}

.contact-form > textarea {
	height: 150px;
	max-width: 100%;
	min-height: 100px;
}

.contact-form > label {
	padding-bottom: 5px;
	color: #f7b538;
	font-weight: bold;
	font-family: 'Merienda', cursive;
	font-size: 18px;
}

.contact-form > input:last-of-type {
	font-family: 'The Historia Demo', sans-serif;
	color: #fe4a57;
	background: #1f2235;
	text-decoration: none;
	padding: 7px 14px;
	font-size: 26px;
	width: 150px;
	border-radius: 10px;
	margin: 0 auto;
	border: 3px solid #fe4a57;
}

.contact-form > input:last-of-type:hover {
	background-color: #f7b538;
	color: #1f2235;
	transition: all 0.3s ease-out;
	cursor: pointer;
	border: 3px solid #1f2235;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
