#footer {
	bottom: 0;
	max-width: 100vw;
	width: 100%;
	background-color: #1f2235;
	height: 50px;
	align-content: center;
	font-family: 'Merienda', cursive;
	z-index: 1;
	overflow: hidden;
	margin-top: 20px;
}

#footer p {
	font-size: 17px;
	text-align: center;
	color: #fe4a57;
}

#footer hr {
	width: 75%;
	border: 1px solid #fe4a57;
	margin: 0 auto;
}
